@tailwind base;
@tailwind components;
@tailwind utilities;


.App {
    margin-bottom: 55px;
}

/* 响应式改进 */
@media (max-width: 640px) {
    .App {
        margin-bottom: 20px;
    }
}

/* 改进滚动条样式 */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: #a8a8a8;
}

/* 确保文本在小屏幕上可读性 */
@media (max-width: 640px) {
    body {
        font-size: 14px;
        line-height: 1.5;
    }
    
    h1, h2, h3 {
        line-height: 1.3;
    }
}

/* 改进触摸体验 */
@media (hover: none) {
    button, .cursor-pointer {
        transform: scale(1);
    }
    
    button:active, .cursor-pointer:active {
        transform: scale(0.95);
        transition: transform 0.1s;
    }
}